ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

a:hover {
  color: rgb(58, 58, 58);
}

ul {
  text-align: left;
}

li {
  cursor: pointer;
  padding: 15px 0;
  position: relative;
  color: #d1d1d1;
}

li:hover {
  color: #fff;
}
.isActive {
  color: #fff;
}
.isActive:after {
  content: "âœ”";
  position: absolute;
  color: #b9ecb9;
  right: 0;
  top: 40%;
  font-size: 30px;
  transform: translateY(-50%);
}

body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.select-bar {
  position: fixed;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 8px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  cursor: default;
  right: 20px;
  top: 20px;
  width: 250px;
}

.select-bar:hover {
  cursor: move;
}

.btn-wrapper {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 15px 0;
  justify-content: space-around;
}

.btn {
  border: none;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 5px;
  font-size: 90%;
  cursor: pointer;
  padding: 2px 8px;
  margin: 10px 5px;
}

h3 {
  font-size: 1rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.00938em;
}

.MuiMenuItem-root:hover {
  background-color: rgba(190, 190, 190, 0.733) !important;
}

.MuiListItem-root.Mui-selected {
  background-color: rgba(37, 37, 37, 0.534) !important;
  color: #fff;
}

.MuiSelect-select,
.MuiSelect-icon,
.MuiInputBase-input {
  color: #fff !important;
}

.MuiSelect-select:focus {
  background-color: transparent !important;
}

.MuiInput-underline:before {
  border-bottom: 1px solid #fff !important;
}
